const ProceduralGeneration = () => {
    const proceduralGenerationUnityContext = new UnityContext({
        loaderUrl:"games/ProceduralGeneration.loader.js",
        dataUrl:"games/ProceduralGeneration.data",
        frameworkUrl:"games/ProceduralGeneration.framework.js",
        codeUrl: "games/ProceduralGeneration.wasm",
    })
    return(
        <div className="font-medium px-2">
            <h2 className="text-3xl px-2 py-2">Procedural generation</h2>
            <p className="px-3 py-3">
            W A S D to move camera.
            <br /> Click to generate new seed
            </p>
            <Unity className="mx-auto" unityContext={proceduralGenerationUnityContext} width="100%" height="600px"  />
            <p className=" py-3">
                This project is my hobby exploration project into procedural generation.
                <br />
            </p>
            <p>
                I use this project as a hobby project to deepen my understanding of C# and my game development knowledge. 
                I am currently working on extending the procedural generation part into an infinite voxel based form, with added gameplay elements.
                <br /><br />
                Trees, rocks and plants made by a friend.
            </p>
        </div>)
}



